/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}


* {
    margin: 0;
    padding: 0;
  }
:root {
    --eerie-black: #1B1B1B;
    --anti-flash-white: #f3f3f3;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
      Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 1000;
    
}

header, footer {
    
    height: 75px;
    
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #1B1B1B;
    background-color: var(--eerie-black);
    color: #F3F3F3;
}

header h1 {
    font-size: 2rem;
}

main {
    display: flex;
    justify-content: center;
    margin: 50px 0;
}
form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 25px;
  padding: 25px;
  background-color: var(--anti-flash-white);
  border-radius: 5px;

  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}
form h1 {
  font-weight: 500;
  font-size: 1.25rem;
}

.form-section {
  border-bottom: 1px solid var(--eerie-black);
  padding: 25px 0;
}
.form-row {
  display: flex;
  margin-bottom: 10px;
  width: 100%;
}
.single {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.pair {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 0px;
}
.form-input {
  display: flex;
  flex-direction: column;
  
}
.form-input input {
    height: 1.1rem;
    padding: 3px;
    border: none;
    border-radius: 3px;
    margin-top: 3px;
}
.add-remove-btn {
    width: 100%;
    height: 2rem;
    color: white;
    background-color: var(--eerie-black);
    border: none;
    border-radius: 3px;
    font-weight: 600;
}
button:hover {
  cursor: pointer;
}
.cv {
  border-radius: 3px;
  background-color: whitesmoke;
  padding: 20px;
  width: 595px;
  height: 770px;
  font-family: 'Times New Roman', Times, serif;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}
.cv-row {
    display: flex;
    justify-content: space-between;
    line-height: 1.25rem;
}
.cv-row h3, .from-to {
    font-weight: 600;
    font-size: 1.125rem;
}
.italicize {
    font-style: italic;
}

/*start of personal info section*/
.personal-info {
    padding-bottom: 10px;
    border-bottom: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.personal-info h1 {
    font-size: 2rem;
}
.personal-info h2 {
    font-size: 1rem;
}
.personal-info h2 a {
    text-decoration: none;
    color:rgba(0, 0, 0, 0.75)
}
/*end of personal info section*/
/*start of education section*/
.education-section, .experience-section {
    padding: 10px 0;
}
.education-section {
    border-bottom: 1px solid black;
}
.education-section h1, .experience-section h1 {
    font-weight: 600;
    font-size: 1.25rem;
    margin-bottom: 10px;
}
.education, .experience {
    margin-bottom: 10px;
}
/*end of education section*/
/*start of experience section*/
.tasks {
    margin-top: 10px;
}
/*end of experience section*/
